.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 100%;
    height: 100vh;


    grid-template-areas:
"h h"
"n c";
    grid-template-rows: 70px 1fr;
    grid-template-columns: 1.7fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    grid-area: c;
    background-color: #ebeaec;
}